<template>
  <div class="chat">
    <header class="chat__header">
			<nav class="navbar">
				<button class="navbar__go-back" @click="goBack"></button>
				<h1 class="navbar__heading">Chat</h1>
			</nav>
		</header>
    <section class="chat__body">
			<ul class="chat__messages" v-chat-scroll>
				<li class="message" v-for="message in messages" :key="message.id">
					<h3 class="message__author">{{ message.username}}</h3>
					<p class="message__text">{{ message.message}}.</p>
					<time class="message__time">{{ message.time }}</time>
				</li>
			</ul>			
    </section>
		<section class="chat__add-message">
				<AddMessage :username="username" />
		</section>
  </div>
</template>

<script>
import AddMessage from '@/components/AddMessage'
import database from '@/firebase/init'
import moment from 'moment'
export default {
  name: 'Chat',
	props: ['username'],
	components: {
		AddMessage
	},
  data() {
    return {
			messages: []
    }
	},
	methods: {
		goBack() {
			this.$router.go(-1)
		}
	},
	created() {
		let databaseRef = database.collection("messages").orderBy("time")
		databaseRef.onSnapshot(snapshot => {
			snapshot.docChanges.forEach(change => {
				if (change.type == "added") {
					let doc = change.doc
					this.messages.push({
						id: doc.id,
						username: doc.data().username,
						message: doc.data().message,
						time: moment(doc.data().time).startOf("hour").fromNow()
					})
				}
			})
		})
	}
}
</script>

<style>
.chat {	
	height: 100%;
	display: flex;
	flex-direction: column;
	background: var(--accent) url("../assets/bg-chat.jpg") no-repeat center / cover;
}
.chat__header {
  height: 6.5rem;
  background-color: #000;
}
.navbar {
	color: var(--text-light);
	height: 100%;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 3rem 2rem 1.5rem;
}
.navbar__go-back {
	background: none;
	border: 0;
	display: block;
	width: 40px;
	height: 40px;
	position: relative;
	cursor: pointer;
	color: var(--text-light);
	outline: none;
}
.navbar__go-back:before {
	content: '';
	position: absolute;
	top: 15px;
	left: 10px;
  width: 0;
  height: 0;
  border-top: 7px solid transparent;
  border-bottom: 7px solid transparent;
  border-right: 11px solid currentColor;
	transition: .2s;
}
.navbar__go-back:hover:before,
.navbar__go-back:focus:before {
	border-right: 11px solid var(--accent);
}
.navbar__heading {
	font-size: 2rem;
	font-weight: 400;
}
.chat__body {
	flex-grow: 1;
	overflow: auto;
}
.chat__messages {
	padding: 3rem 2rem;
	display: flex;
	flex-direction: column;
	align-items: flex-end;
}
.message {
	display: block;
	width: 200px;
	padding: 2rem 1rem 1.5rem 2rem;
	background-color: #fff;
	box-shadow: 0 40px 50px rgba(0, 0, 0, 0.2);
	position: relative;
}
.message:not(:last-child) {
	margin-bottom: 3rem;
}
.message__author {
	display: block;
	position: absolute;
	left: -2rem;
	top: -2rem;
	padding: 1rem;
	font-size: 1.5rem;
	font-weight: 700;
	line-height: 1;
	box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
	background-color: #fff;
}
.message__text {
	font-size: 1.8rem;
	word-wrap: break-word;
}
.message__time {
	margin-top: 2rem;
	font-size: 1.2rem;
	color: var(--text-secondary);
}
::-webkit-scrollbar {
  width: 2px;
}
::-webkit-scrollbar-track {
  background: #ccc; 
}
::-webkit-scrollbar-thumb {
  background: #888; 
}
</style>




