<template>
  <div id="app" class="container">
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App'
}
</script>

<style>
/*------ VARIABLES --------*/
html {
  --accent: #ffbdb8;
  --accent-hover: #ffbdb8;
  --text-dark: #000000;
  --text-light: #ffffff;
  --text-secondary: #b49190;
}
/*-------- GLOBAL ---------*/
* {
  box-sizing: border-box;
}
*::before,
*::after {
  box-sizing: inherit;
}
html {
  height: 100%;
  font-size: 10px;
}
body {
  margin: 0;
  height: 100%;
  min-width: 320px;
  font-family: "Karla", Arial, sans-serif;
  font-size: 2rem;
  line-height: 1.5;
  color: var(--text-dark);
  background-color: #fff;
  overflow-x: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}
h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}
ul, ol {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
img,
video,
audio {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
  color: inherit;
}
.container {
  height: 67rem;
  max-width: 37.5rem;
	width: 100%;
}
</style>



